import React, { useState, useEffect } from "react";
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Row,
  CardFooter,
  Button,
  Input,
  FormGroup,
  Label,
  Table,
} from "reactstrap";
import "react-toastify/dist/ReactToastify.css";
import { toast, ToastContainer } from "react-toastify";
import axios from "axios";
import Spinner from "reactstrap/lib/Spinner";

const Productspecs = (props) => {
  const [mainCategoryFromServer, setMainCategoryFromServer] = useState([]);
  const [subCategoryFromServer, setSubCategoryFromServer] = useState([]);
  const [allProductSpecs, setAllProductSpecs] = useState([]);
  const [catID, setcatID] = useState("");
  const [values, setValues] = useState({
    title: "",
    label: "",
  });
  const [submitLoading, setSubmitLoading] = useState(false);

  const handleState = (e) => {
    const { name, value } = e.target;
    setValues((prev) => ({ ...prev, [name]: value }));
  };

  useEffect(() => {
    //all category
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query getAllCategory($page:Int,$limit:Int,$mainCategory:Boolean,$hasParent:Boolean,$catId:ID) {
          getAllCategory(input:{page:$page,limit:$limit,mainCategory:$mainCategory,hasParent:$hasParent,catId:$catId}){
            _id,
            name
          }
        }`,
        variables: {
          page: 1,
          limit: 30,
          mainCategory: true,
          hasParent: false,
          catId: null,
        },
      },
    })
      .then((res) => {
        const { getAllCategory } = res.data.data;
        setMainCategoryFromServer(getAllCategory);
      })
      .catch((err) => console.log(err));
  }, []);

  const handleSubmitMain = (e) => {
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query getAllCategory($page:Int,$limit:Int,$mainCategory:Boolean,$hasParent:Boolean,$catId:ID) {
          getAllCategory(input:{page:$page,limit:$limit,mainCategory:$mainCategory,hasParent:$hasParent,catId:$catId}){
            _id,
            name
          }
        }`,
        variables: {
          page: 1,
          limit: 30,
          mainCategory: false,
          hasParent: true,
          catId: e.target.value,
        },
      },
    })
      .then((res) => {
        const { getAllCategory } = res.data.data;
        setSubCategoryFromServer(getAllCategory);
      })
      .catch((err) => console.log(err));
  };

  const handleSubmit = (e) => {
    if (catID === "") {
      toast.warn("مقدار دسته دوم را باید مشخص کنید");
      return false;
    }

    if (values.title === "") {
      toast.warn("فیلد عنوان مشخصات را باید وارد نمائید");
      return false;
    }

    setSubmitLoading(true);
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
            mutation addproductSpecs($category:ID!,$name:String!,$label:String){
                productSpecs(input:{category:$category,name:$name,label:$label}){
                  status,
                  message
                }
              }
            `,
        variables: {
          category: catID,
          name: values.title,
          label: values.label,
        },
      },
    })
      .then((res) => {
        setSubmitLoading(false);
        if (res.data.errors) {
          const { message } = res.data.errors[0].data[0];
          toast.error(message);
        } else {
          const { message } = res.data.data.productSpecs;
          toast.success(message);
          setValues({ title: "", label: "" });
        }
      })
      .catch((err) => console.log(err));
  };

  useEffect(() => {
    setAllProductSpecs([]);
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query getAllProductSpecs($catId:ID!){
          getAllProductSpecs(catId:$catId){
            name,  
            label
          }
        }
        `,
        variables: {
          catId: catID,
        },
      },
    })
      .then((res) => setAllProductSpecs(res.data.data.getAllProductSpecs))
      .catch((err) => console.log(err));
  }, [catID]);

  return (
    <div className="animated fadeIn">
      <ToastContainer />
      <Row>
        <Col xs="12">
          <Card>
            <CardHeader>اضافه کردن مشخصات </CardHeader>
            <CardBody>
              <FormGroup row>
                <Col xs="3">
                  <FormGroup>
                    <Label htmlFor="Maincategory">دسته اصلی</Label>
                    <Input
                      type="select"
                      name="Maincategory"
                      id="Maincategory"
                      onChange={handleSubmitMain}
                    >
                      <option></option>
                      {mainCategoryFromServer.map((item, index) => (
                        <option key={item._id} value={item._id}>
                          {item.name}
                        </option>
                      ))}
                    </Input>
                  </FormGroup>
                </Col>
                <Col xs="3">
                  <FormGroup>
                    <Label htmlFor="subcategory">دسته دوم</Label>
                    <Input
                      type="select"
                      name="subcategory"
                      id="subcategory"
                      onChange={(e) => setcatID(e.target.value)}
                    >
                      <option></option>
                      {subCategoryFromServer.map((item, index) => (
                        <option key={item._id} value={item._id}>
                          {item.name}
                        </option>
                      ))}
                    </Input>
                  </FormGroup>
                </Col>
                <Col xs="3">
                  <FormGroup>
                    <Label htmlFor="title">عنوان مشخصات</Label>
                    <Input
                      type="text"
                      placeholder="عنوان مشخصات را وارد نمایید"
                      name="title"
                      value={values.title}
                      onChange={(e) => handleState(e)}
                      required
                    />
                  </FormGroup>
                </Col>
                <Col xs="3">
                  <FormGroup>
                    <Label htmlFor="label">توضیح مشخصات</Label>
                    <Input
                      type="text"
                      placeholder="توضیحات مشخصات را وارد نمایید"
                      name="label"
                      value={values.label}
                      onChange={(e) => handleState(e)}
                      required
                    />
                  </FormGroup>
                </Col>{" "}
              </FormGroup>
            </CardBody>
            <CardFooter>
              <Button
                type="submit"
                color="primary"
                size="sm"
                onClick={handleSubmit}
              >
                {submitLoading ? <Spinner size="sm" /> : "ثبت مشخصات"}
              </Button>
            </CardFooter>
          </Card>
        </Col>
      </Row>

      <Row>
        <Col>
          <Card>
            <CardHeader>
              <Row>
                <Col xs="2">
                  <i className="fa fa-align-justify"></i>
                  <span style={{ marginRight: "5px" }}>
                    لیست همه مشخصات محصول
                  </span>
                </Col>
              </Row>
            </CardHeader>
            <CardBody>
              <Table striped responsive size="sm">
                <thead>
                  <tr>
                    <th>عنوان مشخصات</th>
                    <th>توضیحات مشخصات</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    {allProductSpecs.map((item) => (
                      <React.Fragment key={item._id}>
                        <td>{item.name}</td>
                        <td>{item.label}</td>
                      </React.Fragment>
                    ))}
                  </tr>
                </tbody>
              </Table>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default Productspecs;
